import React from 'react'
import {withRouteData, Head, SiteData} from 'react-static'

import TextBlock from "../components/textBlock";
import Menu from "../components/menu";
import Header from "../components/header";
import Footer from "../components/footer";

class Page extends React.Component {
    constructor(props) {
        super(props);
        this.state = {menuIsOpen: false}
    }

    menuToggle() {
        this.setState({menuIsOpen: !this.state.menuIsOpen});
    }

    render() {
        const textClass = 'content__main';

        let classNames = ["handbook off-canvas-menu"];
        if (this.state.menuIsOpen) {
            classNames.push('off-canvas-menu--open');
        }

        return <div className={classNames.join(' ')}>
            <SiteData render={({siteTitle}) => (
                <Head>
                    <title>{siteTitle} | {this.props.page.title}</title>
                </Head>
            )}/>
            <Menu data={this.props.menuItems} page={this.props.page} parent={this}/>
            <div className="handbook__main off-canvas-menu__content">
                <section className="handbook__main__content content">
                    <Header parent={this} title={this.props.page.title}/>
                    <main className={textClass}>
                        <TextBlock text={this.props.page.content}/>
                    </main>
                    <Footer/>
                </section>
            </div>
        </div>
    }
}


export default withRouteData(Page);



