import React from 'react';
import './style.scss';

export default class Header extends React.Component {
    toggle() {
        this.props.parent.menuToggle();
    }

    render() {
        return <header className="content__header">
            <div className="off-canvas-menu__trigger">
                <span className="icon-list" onClick={this.toggle.bind(this)}></span>
            </div>
            <ul className="breadcrumbs" role="navigation" aria-label="breadcrumbs navigation">
                <li className="breadcrumbs__item">
                    <a className="breadcrumbs__link" href="/">
                        Handbook
                    </a>
                </li>

                <li className="breadcrumbs__item">
                    { this.props.title }
                </li>
            </ul>
        </header>
    }
}



