import React from 'react';
import './style.scss';
import {withRouteData} from "react-static";

class MenuSections extends React.Component {
    render() {
        return this.props.sections.data.map((section, index) => {
            return <MenuSection section={section} key={index} slug={this.props.slug} />;
        });
    }
}

class MenuSection extends React.Component {
    render() {
        const menuItems = this.props.section.items.map((item) => {
            return <MenuItem item={item} key={item.menu_item.id} slug={this.props.slug} />;
        });
        const sectionTitle = (typeof this.props.section.primary.menu_section_title[0] !== 'undefined') ? this.props.section.primary.menu_section_title[0].text : '[Title not set]';
        return <li className="menu__item">
            {sectionTitle}
            <ul className="menu">
              {menuItems}
            </ul>
        </li>
    }
}

class MenuItem extends React.Component {
    render() {
        let classNames = 'menu__item';
        let menuItemSlug = null;
        if (typeof this.props.item.menu_item.data !== 'undefined') {
          menuItemSlug = this.props.item.menu_item.data.slug;
        }
        const menuLink = (menuItemSlug !== null) ? '/' + menuItemSlug : '#';
        if (menuLink === '/' + this.props.slug) {
            classNames += ' menu__item--active';
        }
        const menuLinkTitle = (typeof this.props.item.menu_item_title[0] !== 'undefined') ? this.props.item.menu_item_title[0].text : '[Title not set]';
        return <li className={classNames}>
            <a className="menu__link" href={menuLink}>{menuLinkTitle}</a>
          </li>
    }
}

class Menu extends React.Component {
    toggle() {
        this.props.parent.menuToggle();
    }

    render() {
        return <div className="handbook__sidebar off-canvas-menu__menu">
            <div className="sidebar">
                <div className="off-canvas-menu__close-trigger" onClick={this.toggle.bind(this)}>
                    <span className="icon-close"></span>
                </div>
                <div className="sidebar__logo logo">
                    <a href="/" className="logo__image">Deeson</a>
                </div>
                <nav className="sidebar__menus nav-block">
                    <ul className="nav-block___menu menu" role="navigation" aria-label="main navigation">
                        <li className="menu__item">
                            <a className="menu__link" href="/">Home</a>
                        </li>
                        <MenuSections sections={this.props.menuItems[0]} slug={this.props.page.slug} />
                    </ul>
                </nav>
            </div>
        </div>
    }
}

export default withRouteData(Menu);



